                                    <p style="font-family:Inter;font-weight:900;font-style:italic;text-align:center;letter-spacing:19px"
                                        class="jsx-fd5fd9bccf3c73d3"><span style="color: #e62626;">T</span>PXN GHOST DIVISION</p><span
                                        class="jsx-fd5fd9bccf3c73d3 paragraph">We are none of us alike, and yet we are
                                        the same. We are from the ends of the earth and everywhere in between. We were
                                        once strangers, but here we are together, united by a singular belief - our
                                        world is about to change.<br class="jsx-fd5fd9bccf3c73d3" /><br
                                            class="jsx-fd5fd9bccf3c73d3" /><br class="jsx-fd5fd9bccf3c73d3" />You called
                                        to us, with the Twenty by your side. You were the start of this, but we are the
                                        ones who will continue the fight. We are the ones who will see it through.<br
                                            class="jsx-fd5fd9bccf3c73d3" /><br class="jsx-fd5fd9bccf3c73d3" /><br
                                            class="jsx-fd5fd9bccf3c73d3" />Twenty there were, with the Origin. But now
                                        we are more. So many more.<br class="jsx-fd5fd9bccf3c73d3" /><br
                                            class="jsx-fd5fd9bccf3c73d3" /><br class="jsx-fd5fd9bccf3c73d3" />We stand
                                        as one, 10,000 strong. We stand with you.<br class="jsx-fd5fd9bccf3c73d3" /><br
                                            class="jsx-fd5fd9bccf3c73d3" /><br
                                            class="jsx-fd5fd9bccf3c73d3" />Ghosts.</span>
                                </div>
