                                <div class="jsx-9cdd8a78b28f8a10">
                                    <div class="jsx-9cdd8a78b28f8a10 specialForce-body-desc">
                                        <div class="jsx-9cdd8a78b28f8a10"><span
                                                class="jsx-9cdd8a78b28f8a10 specialForce-origins">FIND US</span></div>
                                        <div style="margin-top:1rem" class="jsx-9cdd8a78b28f8a10"><span
                                                class="jsx-9cdd8a78b28f8a10 specialForce-title">We are the
                                                Resistance.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">There are 100 Regiments each led by a
                                                Phantom, with each regiment comprised of 100 Ghosts under the Phantom’s
                                                charge.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">Regiments are independent of one another,
                                                and the nature of their operations will depend upon the personalities of
                                                the Ghosts within that regiment and of the Phantom leading them. Each
                                                regiment will have different skills, strengths and focus from that of
                                                another.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">Every Regiment will have a stipend of $<span style="color: #e62626;">T</span>PXN
                                                tokens to be distributed by the Phantom to members of the group, for
                                                achievements, contributions or actions deemed beneficial to the success
                                                and growth of the regiment. This may be as a result of mission
                                                completion, tasks completed, or general behaviour that the Phantom feels
                                                worthy of reward.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc italic"><span
                                                class="jsx-9cdd8a78b28f8a10">Within each regiment, Ghosts can improve
                                                their ranking status to earn more $<span style="color: #e62626;">T</span>PXN tokens, as a higher rank
                                                generates more daily token rewards.</span></div>
                                        <div style="margin:4rem 0" class="jsx-9cdd8a78b28f8a10">
                                            <div class="jsx-9cdd8a78b28f8a10 specialForce-divider"></div>
                                        </div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">If a revolution can begin with a single
                                                spark, a single man, then what can we achieve with twenty?</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">I searched for them around the world -
                                                those who would heed the call, the ones who would fight by my side. I
                                                searched for them, far and wide, and I found them.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">They understood my purpose, they believed
                                                in what I wished to achieve. They gave me hope this could be
                                                done…</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">And now we are here, at the dawn of this
                                                new age, ready to face the world we are destined to change.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">They are sure to rise against us, and there
                                                is conflict to be had, but they will not succeed - nothing can stand in
                                                our way. For in the shadows, a storm rages…</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">I am Rei, The Origin. These are my
                                                Phantoms.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc"><span
                                                class="jsx-9cdd8a78b28f8a10">We are <span style="color: #e62626;">T</span>PXN.</span></div>
                                        <div class="jsx-9cdd8a78b28f8a10 specialForce-desc bold"><span
                                                class="jsx-9cdd8a78b28f8a10">Find Us.</span></div>
                                    </div>
                                </div>
                            </div>
                        </div>
